<template>
  <div class="ysServicesList">
    <div class="page-header-main">
      <mt-header>
        <a @click="back" slot="left" style="padding-right:10px;">
          <mt-button icon="back">月嫂服务列表</mt-button>
        </a>
      </mt-header>
    </div>
    <div class="ysServicesListBody">
      <p>请选择服务类型</p>
      <div class="ysServicesListBodyMain">
        <p v-for="service in ysServices" @click="handleClick(service.id, service.servicesName)">{{service.servicesName}}</p> 
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
export default {
  name: 'more',
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters({
      globalVal: 'getGlodalVal',
      ysServices: 'getYsServices'
    })
  },
  methods: {
    ...mapActions({
      getMomSercices: 'getMomSercices',
      changeCompanyType: 'changeCompanyType'
    }),
    back: function() {
      history.go(-1);
    },
    handleClick: function(id,servicename) {
      this.$router.push({path: '/appointment', query: { id: id, type: 'ys' }})
      this.changeCompanyType(servicename);
    }
  },
  created() {
    this.getMomSercices();
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.ysServicesListBody {
  margin-top: 0.5rem;
}
.ysServicesListBody p {
  font-size: 0.3rem;
  color: #a19ba1;
  height: 0.8rem;
  line-height: 0.8rem;
  padding-left: 0.3rem;
  border-bottom: 2px solid #a19ba1;
}
.ysServicesListBody .ysServicesListBodyMain p {
  font-size: 0.4rem;
  height: 1.2rem;
  line-height: 1.2rem;
  padding-left: 0.6rem
}
</style>



